*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
  line-height: 1.5;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

details summary {
  cursor: pointer;
}

details summary > * {
  display: inline;
}

select {
  appearance: none;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  background-color: #0000;
  border: none;
  width: 100%;
  margin: 0;
  padding: 0 1em 0 0;
}

body {
  background-color: var(--body-background-color);
  color: var(--body-foreground-color);
  font-family: var(--body-font-family, system-ui), serif;
  font-size: var(--body-font-size);
  place-items: center;
  display: grid;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
}

a {
  color: inherit;
}

nav a {
  text-decoration: none;
}

summary {
  list-style: none;
}

button {
  font: inherit;
  cursor: pointer;
  color: currentColor;
  appearance: none;
  background: none;
  border: 0;
}

[hidden] {
  display: none !important;
}

:focus {
  outline: 2px solid #0000;
}

:focus-visible {
  outline-color: var(--outline-color, currentColor);
}

ul:not([role="list"]) {
  padding-left: 1rem;
}

.skip-link:focus {
  z-index: 1000;
  background-color: var(--skip-link-background-color);
  box-shadow: var(--skip-link-box-shadow);
  font-size: var(--skip-link-font-size);
  padding: .5rem 1rem;
  text-decoration: none;
  display: inline-block;
  position: absolute;
  top: 1rem;
  left: 1rem;
}

.main-container[data-page="home"]:not(:target) .main {
  min-width: 0;
  min-height: 0;
  display: block;
}

.main-container:focus {
  outline: none;
}

.main-container:focus .main {
  outline: 2px solid;
}

[data-page="home"] .main {
  --padding-x: 2rem;
  --padding-y: 2rem;
  margin: var(--padding-x) var(--padding-y);
}

.main-container[data-page="home"] {
  --padding-x: 0;
  --padding-y: 0;
}

.main-container {
  flex: 1;
  justify-content: center;
  display: flex;
}

:not([data-page="home"]) + .root .main-container {
  --padding-x: 0;
  --padding-y: 0;
}

@media (width >= 43.75em) {
  :not([data-page="home"]) + .root .main-container {
    --padding-x: 2rem;
    --padding-y: 2rem;
  }
}

.footer {
  background-color: var(--taskbar-background-color);
  box-shadow: var(--footer-box-shadow);
}

.footer__inner {
  place-content: center flex-start;
  gap: .1875rem;
  padding: .25rem .375rem;
  display: flex;
  position: relative;
}

.root {
  background-color: var(--root-background-color);
  grid-template-rows: 1fr auto;
  width: 100%;
  height: 100%;
  display: grid;
  overflow: hidden;
}

@media (width >= 43.75em) {
  .root {
    max-width: min(100vw, var(--root-max-width));
    max-height: min(100vh, var(--root-max-height));
    overflow: auto;
  }
}

.root__desktop {
  gap: var(--gap, 0rem);
  background-image: var(--wallpaper, none);
  background-size: var(--size, 20px 20px);
  background-repeat: var(--repeat, repeat);
  background-position: var(--position, auto);
  display: flex;
  position: relative;
  overflow: auto;
}

@media (width >= 43.75em) {
  .root__desktop {
    --gap: 1rem;
  }
}

.root__taskbar {
  background-color: var(--taskbar-background-color);
  position: sticky;
  bottom: 0;
  left: 0;
}

.nav__list {
  justify-content: start;
  display: grid;
}

.nav__list[data-parent="header"] {
  --icon-min-size: 4.75rem;
  --gap: .75rem;
  gap: var(--gap);
  grid-template-columns: auto auto;
}

.header .nav__link, .settings .nav__link {
  text-align: center;
  color: #fff;
  justify-content: center;
  justify-items: center;
  gap: .25rem;
  display: grid;
}

.nav__link {
  pointer-events: all;
}

.nav__link[aria-hidden] {
  opacity: 1;
}

.nav__link[data-draggable] {
  opacity: 1;
  -webkit-user-select: none;
  user-select: none;
  position: absolute;
  inset: 0;
}

.nav__link[data-draggable]:focus-visible {
  opacity: 1;
}

.nav__link.on-drag {
  opacity: .6;
}

.header .nav__link--name, .settings .nav__link--name {
  background-color: #00000073;
  padding: 0 .25ch;
  line-height: 1;
}

.nav__item {
  -webkit-user-select: none;
  user-select: none;
  pointer-events: none;
  position: relative;
}

.nav__item img {
  -webkit-user-select: none;
  user-select: none;
}

@media screen and (width >= 43.75em) {
  [data-parent="header"] .nav__item[data-item="html"] .nav__link {
    transform: translate(var(--html-position-x, 0), var(--html-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="css"] .nav__link {
    transform: translate(var(--css-position-x, 0), var(--css-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="javascript"] .nav__link {
    transform: translate(var(--javascript-position-x, 0), var(--javascript-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="accessibility"] .nav__link {
    transform: translate(var(--accessibility-position-x, 0), var(--accessibility-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="animation"] .nav__link {
    transform: translate(var(--animation-position-x, 0), var(--animation-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="eleventy"] .nav__link {
    transform: translate(var(--eleventy-position-x, 0), var(--eleventy-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="svg"] .nav__link {
    transform: translate(var(--svg-position-x, 0), var(--svg-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="git"] .nav__link {
    transform: translate(var(--git-position-x, 0), var(--git-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="colors"] .nav__link {
    transform: translate(var(--colors-position-x, 0), var(--colors-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="design"] .nav__link {
    transform: translate(var(--design-position-x, 0), var(--design-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="performance"] .nav__link {
    transform: translate(var(--performance-position-x, 0), var(--performance-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="assets"] .nav__link {
    transform: translate(var(--assets-position-x, 0), var(--assets-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="tools"] .nav__link {
    transform: translate(var(--tools-position-x, 0), var(--tools-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="react"] .nav__link {
    transform: translate(var(--react-position-x, 0), var(--react-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="other"] .nav__link {
    transform: translate(var(--other-position-x, 0), var(--other-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="settings"] .nav__link {
    transform: translate(var(--settings-position-x, 0), var(--settings-position-y, 0));
  }

  [data-parent="header"] .nav__item[data-item="about"] .nav__link {
    transform: translate(var(--about-position-x, 0), var(--about-position-y, 0));
  }
}

.menu__toggle {
  box-shadow: var(--menu-toggle-inactive-box-shadow);
  align-items: center;
  width: fit-content;
  padding: .33rem .375rem;
  display: flex;
}

.menu__toggle img {
  filter: invert(var(--start-btn-invert, 0));
}

.menu[open] .menu__toggle {
  box-shadow: var(--menu-toggle-active-box-shadow);
}

.menu__content {
  min-width: var(--menu-width);
  max-height: calc(min(var(--root-max-height), 100vh)  - var(--taskbar-height));
  background-color: var(--menu-background-color);
  box-shadow: var(--menu-box-shadow);
  background-image: var(--menu-gradient);
  background-repeat: no-repeat;
  background-size: 1.5rem;
  padding: .125rem .125rem .125rem 1.5rem;
  font-size: .9rem;
  position: absolute;
  bottom: 1.85rem;
  left: 1px;
  overflow: auto;
}

.menu .nav__list {
  grid-template-columns: 1fr;
  justify-items: stretch;
  gap: 0;
  font-size: .9em;
  display: grid;
}

.menu .nav__link {
  justify-content: flex-start;
  align-items: center;
  gap: .5rem;
  width: 100%;
  height: 2rem;
  padding: .125rem 2.8125rem .125rem .625rem;
  display: flex;
}

.menu .nav__item:hover {
  background-color: var(--menu-item-hover-background-color);
  color: var(--menu-item-hover-foreground-color);
}

.window {
  --padding-x: .75rem;
  --padding-y: .75rem;
  background-color: var(--window-background-color);
  z-index: 1;
  box-shadow: var(--window-box-shadow);
  grid-template-rows: auto auto 1fr;
  width: 100%;
  display: grid;
  overflow: auto;
}

@media (width >= 43.75em) {
  .window {
    resize: both;
    max-width: min(100%, var(--max-window-width));
    max-height: min(100%, var(--max-window-height));
    min-width: min(100vw, var(--min-window-width));
    min-height: min(100vh, var(--min-window-height));
  }
}

.window__header {
  background-image: var(--window-header-background-image);
  box-shadow: var(--window-header-box-shadow);
  color: var(--window-header-foreground-color);
  align-items: center;
  gap: 1ch;
  margin: 2px 2px 0;
  padding: 0 .1rem 0 .5ch;
  display: flex;
}

.window__heading {
  flex: 1;
}

.window__navbar {
  align-items: center;
  margin: 2px .25rem;
  font-size: .75rem;
  line-height: 1.3;
  display: flex;
}

.window__navbar span {
  margin: 0 1px;
  padding: 2px 1ch;
  display: inline-block;
}

.window__navbar span:first-letter {
  text-decoration: underline;
}

.window__navbar span:hover {
  box-shadow: var(--menu-toggle-active-box-shadow);
}

.window__content {
  border-top: 1px solid var(--window-content-border-top-color);
  margin: 0 2px 5px;
  overflow: auto;
}

.window__content .empty-state {
  padding: 1rem;
}

.window[data-item="window"] {
  transform: translate(var(--window-position-x, 0), var(--window-position-y, 0));
}

.bookmark {
  --padding-x: .75rem;
  padding: var(--padding-x) var(--padding-y);
  box-shadow: var(--bookmark-box-shadow);
  gap: .25rem;
  margin: .75rem 0;
  display: grid;
}

.bookmark:first-child {
  margin-top: 0;
}

.bookmark:last-child {
  margin-bottom: 0;
}

.bookmark[data-clickable] {
  cursor: pointer;
}

.bookmark[data-clickable] a {
  text-decoration: none;
}

.bookmark[data-clickable]:hover {
  box-shadow: var(--bookmark-hover-box-shadow);
  backdrop-filter: brightness(.97);
}

:not([data-page="home"]) + .root .header {
  display: none;
}

@media (width >= 43.75em) {
  :not([data-page="home"]) + .root .header {
    display: inline-block;
  }
}

[data-overlap] .icon {
  display: block;
  position: relative;
}

[data-overlap] .icon:after {
  content: "";
  z-index: 5;
  background-image: linear-gradient(45deg, #0000 45%, red 46% 54%, #0000 55%);
  border-radius: 50%;
  width: 1.25em;
  height: 1.25em;
  position: absolute;
  bottom: 0%;
  left: 0%;
  transform: translate(-25%, 25%);
  box-shadow: inset 0 0 0 3px red;
}

:not([data-page="home"]) + .root .settings {
  display: none;
}

@media (width >= 43.75em) {
  :not([data-page="home"]) + .root .settings {
    align-items: flex-start;
    display: flex;
  }
}

.switcher {
  box-shadow: var(--switcher-box-shadow);
  border: none;
  border-radius: 1px;
  flex-direction: column;
  justify-content: space-evenly;
  margin: .5rem;
  display: flex;
}

.switcher__preview {
  justify-content: center;
  align-items: flex-start;
  padding: 1rem;
  display: flex;
  position: relative;
}

.switcher [id="wallpaper-preview"] {
  background-size: 16px 16px;
  background-color: var(--root-background-color, #000);
  background-image: var(--wallpaper, none);
  background-size: var(--preview-size, 22.5% auto);
  background-repeat: var(--repeat, repeat);
  background-position: var(--position, auto);
  width: 153px;
  height: 112px;
  position: absolute;
  top: 2rem;
  transform: translateX(-1px);
}

.switcher [id="wallpaper-preview"]:after {
  content: "";
  background-image: var(--theme-preview, url("../default.1c9d614d.png"));
  background-size: 100% 100%;
  position: absolute;
  inset: 0;
}

.switcher__form--inner {
  padding: .5rem;
  font-size: .85em;
  position: relative;
}

.switcher__heading {
  background-color: var(--window-background-color);
  font-weight: 400;
  display: inline-block;
  position: absolute;
  top: .6rem;
  left: 1.25rem;
  transform: translateY(-50%);
}

.switcher__settings {
  box-shadow: var(--switcher-fieldset-box-shadow);
  border: 1px solid #0000;
  flex-wrap: wrap;
  gap: .25rem 1rem;
  padding: .75rem;
  display: flex;
}

.switcher__fieldset {
  border: 0;
  flex: 1;
  gap: .25rem 1rem;
  height: 8rem;
  display: grid;
  position: relative;
}

.switcher__fieldset legend {
  margin-bottom: .25rem;
}

.switcher__wallpapers {
  background-color: var(--switcher-wallpapers-background-color, #fff);
  box-shadow: var(--switcher-wallpapers-box-shadow);
  border: 1px solid #0000;
  width: 100%;
  height: 100%;
  padding: 5px;
  overflow: auto;
}

.switcher__wallpapers[data-placeholder] {
  opacity: 0;
}

.switcher__wallpapers:not([data-placeholder]) {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.switcher .wallpaper {
  gap: .1rem;
  display: block;
}

.switcher .wallpaper input[type="radio"] {
  opacity: .00001;
  width: 1rem;
  height: 1rem;
  position: absolute;
}

.switcher .wallpaper input[type="radio"].applied ~ span {
  border: 1px dotted;
}

.switcher .wallpaper input[type="radio"]:focus ~ span {
  background-color: var(--switcher-input-active-background-color);
  color: #fff;
  margin: 0;
}

.switcher .wallpaper img {
  vertical-align: text-bottom;
  width: 1rem;
  height: 1rem;
  display: inline;
}

.switcher__controls {
  flex-direction: column;
  justify-content: space-between;
  align-self: flex-end;
  height: 6.75rem;
  display: flex;
}

.switcher__browse {
  position: relative;
}

.switcher__browse input[name="browse"] {
  opacity: 0;
  width: 0;
  min-width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.switcher__buttons {
  justify-content: flex-end;
  align-items: center;
  gap: .5rem;
  margin-top: 1rem;
  display: flex;
}

.switcher__button {
  background-color: var(--buttons-background-color);
  box-shadow: var(--buttons-box-shadow);
  cursor: pointer;
  border-radius: 0;
  flex: 0 11ch;
  padding: .15rem 1.25rem;
  display: block;
}

.switcher__button[id="settings-ok-btn"] {
  box-shadow: var(--ok-button-box-shadow);
}

.switcher select {
  width: 100%;
  display: block;
}

