  --menu-toggle-active-box-shadow: inset -1px -1px 0 #fff, inset 1px 1px 0 #ffb3c1, inset -2px -2px 0 #ffb3c1, inset 2px 2px 0 #ff4d6d;
  --menu-box-shadow: inset -1px -1px 0 #ff4d6d, inset 1px 1px 0 #ffc6c7, inset -2px -2px 0 #ffb3c1, inset 2px 2px 0 #fff;
  --bookmark-hover-box-shadow: inset -1px 0 #f5f5f5, inset 0 -1px #f5f5f5, inset 1px 0 #ff4d6d, inset 0 1px #ff4d6d;
