.popup {
  --max-window-width: 25rem;
  --max-window-height: 100vh;
  top: var(--top, 0);
  grid-template-rows: auto 1fr;
  height: 100%;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
}

@media (width >= 43.75em) {
  .popup {
    --top: 2rem;
    height: unset;
  }
}

.alert {
  max-height: initial;
  min-height: initial;
  min-width: inherit;
  resize: none;
  width: auto;
  max-width: min(100%, 20rem);
  height: auto;
  font-size: .85rem;
}

.alert-container {
  z-index: 999;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.alert__content {
  padding: 1rem;
}

.alert__content button {
  margin-left: auto;
  margin-right: auto;
}

.visually-hidden, .skip-link:not(:focus), .main-container[data-page="home"]:not(:target) .main {
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: 0;
  padding: 0;
  position: fixed;
  overflow: hidden;
}

.wrapper {
  padding: var(--padding-x, 1rem) var(--padding-y, 1rem);
}

.flow > * + * {
  margin-top: var(--flow-space, .5rem);
}

.divider {
  color: inherit;
  border: 0;
  border-bottom: 1px solid #929292;
  margin: 1rem 0;
}

.scrollbar ::-webkit-scrollbar-thumb {
  background: var(--scrollbar-color);
  border-top: 1px solid var(--scrollbar-border-color-light);
  border-right: 1px solid var(--scrollbar-border-color-dark);
  border-bottom: 1px solid var(--scrollbar-border-color-dark);
  border-left: 1px solid var(--scrollbar-border-color-light);
  box-shadow: inset -1px 0 var(--scrollbar-color), inset -1px 1px #fff, inset -1px -1px var(--scrollbar-color), inset 0 0 0 1px #fff;
  border-radius: 0;
  width: 16px;
  height: 40px;
}

.scrollbar ::-webkit-scrollbar-track {
  background: linear-gradient(var(--scrollbar-color) 0 50%, #fff0 50% 100%) 1px 0 / 1px 2px, linear-gradient(to right, var(--scrollbar-color) 0 50%, #fff 50% 100%) 0 0 / 2px 1px;
  border: 1px solid var(--scrollbar-border-color-dark);
  border-bottom-color: #fff;
  border-right-color: #fff;
  width: 16px;
  padding: 14px 0;
}

.scrollbar ::-webkit-scrollbar {
  width: 16px;
}

.scrollbar ::-webkit-scrollbar-button:single-button {
  background-color: var(--scrollbar-color);
  border-top: 1px solid var(--scrollbar-border-color-light);
  border-right: 1px solid var(--scrollbar-border-color-dark);
  border-bottom: 1px solid var(--scrollbar-border-color-dark);
  border-left: 1px solid var(--scrollbar-border-color-light);
  box-shadow: inset -1px 0 var(--scrollbar-color), inset -1px 1px #fff, inset -1px -1px var(--scrollbar-color), inset 0 0 0 1px #fff;
  width: 16px;
  height: 16px;
  display: block;
}

.scrollbar ::-webkit-scrollbar-button:single-button:active {
  border: 1px solid var(--scrollbar-border-color-dark);
}

.scrollbar ::-webkit-scrollbar-button:single-button:vertical:decrement {
  background: linear-gradient(-135deg, var(--scrollbar-color) 0% 50%, transparent 50% 100%) 0 25% / 100% 50% no-repeat, linear-gradient(135deg, transparent 0% 50%, var(--scrollbar-border-color-dark) 50% 100%) 0 25% / 100% 50% no-repeat, var(--scrollbar-color);
}

.scrollbar ::-webkit-scrollbar-button:single-button:vertical:increment {
  background: linear-gradient(-45deg, var(--scrollbar-color) 0 50%, transparent 50% 100%) 0 75% / 100% 50% no-repeat, linear-gradient(45deg, transparent 0 50%, var(--scrollbar-border-color-dark) 50% 100%) 0 75% / 100% 50% no-repeat, var(--scrollbar-color);
}

.scrollbar ::-webkit-scrollbar-button:single-button:horizontal:decrement {
  background: linear-gradient(-45deg, transparent 0 50%, var(--scrollbar-color) 50% 100%) 25% 0 / 50% 100% no-repeat, linear-gradient(45deg, transparent 0 50%, var(--scrollbar-border-color-dark) 50% 100%) 25% 0 / 50% 100% no-repeat, var(--scrollbar-color);
}

.scrollbar ::-webkit-scrollbar-button:single-button:horizontal:increment {
  background: linear-gradient(-45deg, silver 0 50%, transparent 50% 100%) 75% 0 / 50% 100% no-repeat, linear-gradient(45deg, var(--scrollbar-border-color-dark) 0 50%, transparent 50% 100%) 75% 0 / 50% 100% no-repeat, silver;
}

.fl-underline {
  display: block;
}

.fl-underline:first-letter {
  text-decoration: underline;
}

.dropdown {
  --arrow-box-size: 1rem;
  position: relative;
}

.dropdown select {
  box-shadow: var(--dropdown-box-shadow);
  background-color: var(--dropdown-background-color, #fff);
  color: var(--dropdown-foreground-color, var(--body-foreground-color));
  border: 0;
  outline: none;
  padding: 1px 4px;
  font-size: .95em;
  display: block;
}

.dropdown select:focus {
  background-color: var(--dropdown-focus-background-color);
  color: var(--dropdown-focus-foreground-color);
}

.dropdown select:focus option {
  background-color: var(--dropdown-background-color, #fff);
  color: var(--dropdown-foreground-color, var(--body-foreground-color));
}

.dropdown:before, .dropdown:after {
  content: "";
  aspect-ratio: 1;
  min-width: var(--arrow-box-size);
  pointer-events: none;
  height: calc(100% - 4px);
  display: block;
  position: absolute;
  top: 2px;
  bottom: 2px;
  right: 2px;
}

.dropdown:before {
  background-color: var(--dropdown-arrow-box-background-color);
  box-shadow: var(--dropdown-arrow-box-box-shadow);
}

.dropdown:after {
  clip-path: polygon(30% 40%, 70% 40%, 50% 60%);
  background-color: currentColor;
}

html.is-changing .transition-fade {
  opacity: 1;
  transition: opacity .1ms;
}

html.is-animating .transition-fade {
  opacity: 0;
}

.noise:after {
  content: "";
  opacity: .075;
  mix-blend-mode: screen;
  z-index: 99999;
  pointer-events: none;
  background-image: url("../noise.ca5d8238.gif"), url("../noise.26288900.png");
  background-size: 400px;
  width: 100vw;
  height: 100vh;
  position: fixed;
  inset: 0;
}

@media (prefers-reduced-motion) {
  .noise:after {
    background-image: url("../noise.26288900.png");
  }
}
